@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&family=Open+Sans&family=Raleway:wght@400;500&display=swap);
.card-item{position:relative;border:1px solid #e9e9e9;padding:196px 20px 17px}.card-item:hover{cursor:pointer;transition:box-shadow .08s ease-in-out;box-shadow:0px 5px 10px 0px rgba(0,0,0,0.15)}.card-item:hover .image-wrap .overlay{background:#1cb8ff;opacity:0.7}.card-item:hover .image-wrap img{transform:scale(1.3)}.card-item .image-wrap{position:absolute;overflow:hidden;top:-1px;left:-1px;width:calc(100% + 2px);height:180px;font-size:0}.card-item .image-wrap .overlay{display:block;position:absolute;z-index:1;top:0;left:0;width:100%;height:100%;transition:display .08s ease-in-out;background:rgba(0,0,0,0.5);background:linear-gradient(180deg, rgba(255,255,255,0) 0%, #000 100%)}.card-item .info{display:flex;align-items:center;justify-content:space-between}.card-item__image{display:block;width:100%;height:100%;object-fit:cover;transform:scale(1);transition:transform .08s ease-in-out}.card-item__city{position:absolute;z-index:2;top:150px;font-size:13px;color:#ffffff;font-weight:500;line-height:22px}.card-item__title{font-size:16px;line-height:22px;font-family:"Montserrat",sans-serif;color:#33363e;margin-bottom:17px}.card-item__category{font-size:13px;line-height:22px;font-weight:500;color:#a6a9af}.card-item__price{font-size:16px;line-height:22px;color:#282d3b;font-family:"Montserrat",sans-serif}

.range-slider-wrap{position:relative;width:240px}.multi-range-slider{margin-bottom:33px}.multi-range-slider input[type=range]{position:absolute;pointer-events:none;cursor:pointer;-webkit-appearance:none;z-index:2;height:4px;width:100%;opacity:0;margin:0}.multi-range-slider input[type=range]::-webkit-slider-thumb{pointer-events:all;width:16px;height:16px;border-radius:50%;border:0 none;background-color:red;-webkit-appearance:none}.multi-range-slider .slider{position:relative;z-index:1;height:4px}.multi-range-slider .slider .track{position:absolute;z-index:1;left:0;right:0;top:0;bottom:0;background-color:#d6d9e1}.multi-range-slider .slider .range{display:block;position:absolute;z-index:2;top:0;bottom:0;border-radius:5px;background-color:#1cb8ff;transform:translateX(-6px)}.multi-range-slider .slider .thumb{display:block;position:absolute;z-index:3;width:16px;height:16px;background-color:#ffffff;border:2px solid #1cb8ff;border-radius:50%;transform:translate(0, -6px)}.multi-range-slider .slider .thumb.left{transform:translate(-16px, -6px)}.multi-range-slider .slider .thumb.right{transform:translate(4px, -6px)}.range-price{display:flex;align-items:center;font-family:"Montserrat",sans-serif;font-size:16px;color:#33363e}.range-price__divider{margin:0 20px}

.sidebar-section{padding-bottom:50px}.sidebar-section__title{font-size:18px;font-family:"Montserrat",sans-serif;color:#33363e;font-weight:700;text-transform:uppercase;margin-bottom:20px}@media screen and (max-width: 480px){.sidebar-section{padding-bottom:25px}}

.button{display:block;border:unset;font-size:14px;font-family:"Montserrat",sans-serif;text-transform:uppercase;color:#ffffff;background:#1cb8ff;opacity:1;transition:opacity .08s ease-in-out;padding:8px 27px}.button:hover{opacity:.8}.button:focus{outline:unset}

.checkbox-wrap{position:relative}.checkbox-wrap input{position:absolute;opacity:0;cursor:pointer;height:0;width:0}.checkbox-wrap .checkbox{display:flex;align-items:center;cursor:pointer;-webkit-user-select:none;-ms-user-select:none;user-select:none}.checkbox-wrap .checkbox__icon{position:relative;display:block;width:16px;height:16px;background:rgba(76,80,91,0);margin-right:8px}.checkbox-wrap .checkbox__icon::after{content:'';display:block;width:100%;height:100%;position:absolute;top:0;left:0}.checkbox-wrap .checkbox__label{display:block;margin-right:8px}.checkbox-wrap .checkbox__count{display:block;line-height:19px;font-family:"Open Sans",sans-serif;color:#a6a9af}

.category-list__item:not(:last-child){margin-bottom:15px}

.no-data-wrap{display:flex;align-items:center;justify-content:center;flex-direction:column;width:100%;height:100%;position:absolute;top:50%;left:50%;transform:translate(-50%, -50%)}@media screen and (max-width: 609px){.no-data-wrap{position:unset;top:unset;left:unset;transform:unset}}.no-data-wrap>img{display:block;width:150px;height:150px}.no-data-wrap>p{margin-top:10px;font-size:18px}

.spinner{display:block;position:absolute;top:50%;left:50%;margin:auto;height:50px;width:50px;border:6px solid rgba(0,174,239,0.2);border-top-color:rgba(0,174,239,0.8);border-radius:50%;animation:rotation 0.6s infinite linear}@keyframes rotation{0%{transform:translate(-50%, -50%) rotate(0deg)}100%{transform:translate(-50%, -50%) rotate(359deg)}}.container{min-width:320px;max-width:1170px;margin:0 auto}.content{display:flex;flex-wrap:wrap;padding:50px 0 80px}@media screen and (max-width: 1170px){.content{padding:50px 20px 80px}}@media screen and (max-width: 480px){.content{padding:20px 10px}}.content .sidebar{max-width:270px;width:100%;margin-right:30px}@media screen and (max-width: 480px){.content .sidebar{max-width:100%;margin-right:0}}.content .price-wrap{position:relative}.content .price-wrap .filter-button{position:absolute;right:29px;bottom:-7px}@media screen and (max-width: 480px){.content .price-wrap .filter-button{position:relative;right:unset;bottom:unset;margin-top:20px;width:100%}}.content .reset-button{width:100%;margin-top:50px}.content .main{flex-grow:1;position:relative}.card-list{display:grid;grid-gap:30px;grid-template-columns:repeat(auto-fit, minMax(270px, 1fr))}@media screen and (max-width: 480px){.card-list{grid-gap:15px}}

*,*:before,*:after{box-sizing:border-box}html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font:inherit;font-size:100%;vertical-align:baseline;text-rendering:optimizeLegibility;-webkit-font-smoothing:antialiased;-webkit-text-size-adjust:none;-moz-text-size-adjust:none;text-size-adjust:none}html,body{width:100%;height:100%}body{font-family:"Raleway",sans-serif;font-weight:400;font-size:15px;color:#4c505b}footer,header,nav,section,main{display:block}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table,td,th,tr{border-collapse:collapse;border-spacing:0}input{-webkit-appearance:none;border-radius:0}input:focus,select:focus,textarea:focus{outline:none}button,input[type="button"]{cursor:pointer}a{color:inherit;text-decoration:none}

